body{
  padding: 0px;
  margin: 0px;
  background: #4b6cb7;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #182848, #4b6cb7); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  width: 100%;
  min-height: 100vh;
}

.main-section{
  width: 100%;
  min-height: calc(100vh - 60px);
  display: flex;
}

.main-section--create_counter{
  width: 50%;
  min-height: calc(100vh - 60px);
  display: flex;
  justify-content: center;
  align-items: center;
  
}

.main-section--Todos{  
  width: 50%;
  height: calc(100vh - 60px);
  display: flex;
  justify-content: center;
  align-items: center;
}

.main-section--create_counter-card{
  width: 450px;
  height: 400px;
  background-color: rgba(255,255,255,0.1);
  border-radius: 16px;
  padding: 16px;
}

.main-section--Todos-card{
  width: 450px;
  height: 400px;
  background-color: rgba(255,255,255,0.1);
  border-radius: 16px;
  padding: 16px;
  overflow: scroll;
  overflow-x: hidden;
}

.main-section--Todos-card::-webkit-scrollbar {
  background: transparent;
  width: 8px;
}

.main-section--Todos-card::-webkit-scrollbar-thumb {
  background-color: rgba(255,255,255,0.5);    /* color of the scroll thumb */
  border-radius: 20px; 
  padding:8px 0px 8px 0px;      /* roundness of the scroll thumb */
}


@media (max-width: 1000px) {
  .main-section--Todos{  
    width: 100%;
  }
  .main-section--create_counter{
    display:none;    
  }
}
.TodoCounter-title{
    font-size: 24px;
    text-align: center;
}
.inputCreateTodo{
    background-color: white;
    color: black;
    border-radius: 8px;
    outline: none;
    width: 400px;
    height: 40px;
    margin-bottom: 8px;
    margin-top: 8px;
    border: none;
    text-align: center;
}

::-webkit-input-placeholder {
    text-align: center;
}
ul{
    padding-left: 0px !important;
}
.item{
    display: flex;
    justify-content: space-between;
    margin: 4px 0px 4px 0px;
}

.icon-delete:hover{
    color:white;
    background-color: red;
    cursor: pointer;
    border-radius: 50%;
}

.checkTodo:hover{
    cursor: pointer;   
}

.icon-check--Active{
    color:white;
    background-color: green;
}

.item-p--complete{
   text-decoration: line-through;
}
.CreateTodoButton {
    background-color: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: #FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;
  
    -webkit-transform: rotate(0);
  
            transform: rotate(0);
    transition: .3s ease;
  }
  
  .CreateTodoButton:hover {
    -webkit-transform: rotate(224deg);
            transform: rotate(224deg);
  }

  .CreateTodoButton{
    display:none;    
  }


  @media (max-width: 1000px) {
    .CreateTodoButton{
      display:flex;    
    }
  }
